import React from 'react'

export const Footer = () => {
    let footerStyle = {
        position: "relative",
        width: "100%",
        top: "20vh"
    }
    return (
        <footer className="bg-dark text-light py-3 my-4" style={footerStyle}>
            <p className="text-center">Copyright &copy; My TodosList.com</p>
            
        </footer>
    )
}
