import React, { useState } from 'react';
 

export const AddTodo = ({adding}) => {

    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");

    const submit = (e) =>{
        e.preventDefault();//Page will not reload
       
        if(!title || !desc){
            alert("Title or description cannot be blank");
        }
        else {
            adding(title, desc);
            setTitle("");
        setDesc("");
        }
        
       

    }
    return (
        <div className="container">
            <h3>Add a Topic</h3>
            <form onSubmit={submit}>
                <div className="mb-3">
                    <label htmlFor="examtitle" className="form-label">Title</label>
                    <input type="text" value={title} onChange={(e)=>{setTitle(e.target.value)}} className="form-control" id="title" aria-describedby="emailHelp" />
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Description</label>
                    <input type="text" value={desc} onChange={(e)=>{setDesc(e.target.value)}} className="form-control" id="desc" aria-describedby="emailHelp" />
                </div>
                <button type="submit" className="btn btn-success btn-sm">Add Todo</button>
            </form>
        </div>
    )
}
